*{
  font-family: 'Roboto', sans-serif;
}

.conteudo {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
  }
  
  .background {
    height: 100vh;
    position: absolute;
    left: 0;
    opacity: 0.04;
  }
  
  .info {
    width: 60%;
    margin-right: 90px;
    margin-top: 40px;
    color: #ffff
  }

  input {
    border: 1px solid #999999;
    border-radius: 22px;
    min-width: 250px;
    width: 85%;
    margin-right: 12px;
    background: transparent;
    font-size: 18px;
    padding: 10px;
    color: white;
  }

  button {
    border: 1px solid #999999;
    border-radius: 22px;
    margin-right: 12px;
    background: #2D333B;
    color: #FFFFFF;
    font-size: 18px;
    padding: 10px;
    cursor: pointer;
  }

  .profile {
    display: flex;
    margin: 40px 0;
  }
  
  .profileimg {
    width: 120px;
    height: 120px;
    border-radius: 60px;
    border: 2px solid #E5E5E5;
  }
  
  .profile h3 {
    color: #999999;
    font-size: 24px;
    margin: 0;
  }

  .profile span {
    display: block;
    color: #999999;
    font-size: 12px;
    margin: 0;
  }

  .profile p {
    color: #999999;
    font-size: 12px;
    margin: 20px 0 0 0;
  }

  .repositorie {
    font-size: 32px;
    color: #FFFFFF;
    margin: 12px 0;
    text-align: center;
  }